import React from "react";
import image from "./Images/Pic1.png";

const About = () => {
  return (
    <div id="about-section">
      <div className="container">
        <div className="about-wrapper">
          <div className="left-about">
            <img src={image} alt="prof-pic" />
          </div>
          <div className="right-about">
            <div className="about-text-wrapper">
              <p id="intro-heading">MY INTRO</p>
              <p id="desc">
                I am Nikhil Baiswar, a web developer focused on crafting great web experiences.
                Designing and Coding have been my passion since the days I started working with
                computers but I found myself into web development. I enjoy creating beautifully
                designed, intuitive and functional websites.
              </p>
              <ul className="about-list">
                <li>
                  <span className="list-name">Name:</span>
                  <span className="list-desc">Nikhil Baiswar</span>
                </li>
                <li>
                  <span className="list-name">Date of Birth:</span>
                  <span className="list-desc">29 November 1997</span>
                </li>
                <li>
                  <span className="list-name">Address:</span>
                  <span className="list-desc">
                    Flat no.6, Dev Building, Near Pleasant Business Park, FC Road, Pune
                  </span>
                </li>
                <li>
                  <span className="list-name">Pincode:</span>
                  <span className="list-desc">411004</span>
                </li>
                <li>
                  <span className="list-name">Email:</span>
                  <span className="list-desc">nikhilbaiswar@gmail.com</span>
                </li>
                <li>
                  <span className="list-name">Phone:</span>
                  <span className="list-desc">7798867557</span>
                </li>
              </ul>
              <div id="hobbies">
                <div className="hobby">
                  <div className="vertical"></div>
                  <div className="circle">
                    <i className="fas fa-gamepad"></i>
                  </div>
                  <p>Gaming</p>
                </div>
                <div className="hobby">
                  <div className="vertical"></div>
                  <div className="circle">
                    <i className="fas fa-play"></i>
                  </div>
                  <p>Anime</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
