import React from "react";
import emailjs from "emailjs-com";

const Contact = () => {
  function sendEmail(e) {
    e.preventDefault();
    console.log(e.target);
    emailjs.sendForm("gmail", "template_2zue5ud", e.target, "user_y0NUJzRXq53qc9ril2cS6").then(
      (result) => {
        console.log(result.text);
      },
      (error) => {
        console.log(error.text);
      }
    );
    e.target.reset();
  }
  return (
    <section className="container" id="contact">
      <h2 className="h1-responsive font-weight-bold text-center my-4">Contact Me</h2>
      <div className="row">
        <div className="col-md-9 mb-md-0 mb-5">
          <form className="contact-form" onSubmit={sendEmail}>
            <div className="row">
              <div className="col-md-6">
                <div className="md-form mb-0">
                  <label htmlFor="name" className="mb-1">
                    Your name
                  </label>
                  <input type="text" id="name" name="name" className="form-control mb-2" />
                </div>
              </div>

              <div className="col-md-6">
                <div className="md-form mb-0">
                  <label htmlFor="email" className="mb-1">
                    Your email
                  </label>
                  <input type="text" id="email" name="email" className="form-control mb-2" />
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="md-form mb-0">
                  <label htmlFor="subject" className="mb-1">
                    Subject
                  </label>
                  <input type="text" id="subject" name="subject" className="form-control mb-2" />
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-md-12">
                <div className="md-form">
                  <label htmlFor="message" className="mb-1">
                    Your message
                  </label>
                  <textarea
                    type="text"
                    id="message"
                    name="message"
                    rows="2"
                    className="form-control md-textarea mb-3"
                  ></textarea>
                </div>
              </div>
            </div>
          </form>
          <div className="text-center text-md-left">
            <input type="submit" value="Send" className="btn btn-primary send-btn"></input>
          </div>
        </div>
        <div className="col-md-3 text-center">
          <ul className="list-unstyled mb-0">
            <li>
              <i className="fas fa-map-marker-alt fa-2x"></i>
              <p>FC Road,Pune, India</p>
            </li>
            <li>
              <i className="fas fa-phone mt-4 fa-2x"></i>
              <p>+91 7798867557</p>
            </li>
            <li>
              <i className="fas fa-envelope mt-4 fa-2x"></i>
              <p>nikhilbaiswar@gmail.com</p>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default Contact;
