import React from "react";
import Construction from "./Images/Construction.jpg";
import Shopping from "./Images/Shopping.jpg";
import IPL from "./Images/IPL.jpg";

const Projects = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-4 mb-4">
          <div className="bg-white rounded-lg shadow">
            <img src={Shopping} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title">Shoplane Webapp</h5>
              <p className="card-text">
                A Shopping website where we can see list of products on homepage. When we select a
                product we can see product details, add the products to buy in the cart wherein we
                can see all the products added with their cost and finally we can place order from
                the cart.
              </p>
              <a
                href="https://nikhilbaiswar.github.io/React-Shoplane/"
                target="_blank"
                className="btn btn-primary web-btn"
              >
                Go to website
              </a>
            </div>
          </div>
        </div>
        <div className="col-lg-4 mb-4">
          <div className="bg-white rounded-lg shadow">
            <img src={IPL} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title">IPL Teams App</h5>
              <p className="card-text">
                IPL team website where we can see list of all IPL teams on homepage. If we click on
                any of the team card we can get the team details with list of all players in that
                team we can search for any team and player and finally we can add our own players
                and teams.
              </p>
              <a
                href="https://nikhilbaiswar.github.io/IPL-Teams-JS/"
                target="_blank"
                className="btn btn-primary web-btn"
              >
                Go to website
              </a>
            </div>
          </div>
        </div>
        <div className="col-lg-4 mb-4">
          <img src={Construction} className="card-img-top" alt="..." />
          <div className="bg-white rounded-lg shadow">
            <div className="card-body">
              <h5 className="card-title">Construction Webpage</h5>
              <p className="card-text">
                A Construction website using HTML and CSS consisting of navbar, header and details
                of the employees working which is completely responsive.
                <br />
                <br />
                <br />
              </p>
              <a
                href="https://nikhilbaiswar.github.io/Nuuk-Project/"
                target="_blank"
                className="btn btn-primary web-btn"
              >
                Go to website
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Projects;
