import React from "react";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

const Skills = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">HTML</h2>
            <CircularProgressbar
              className="prog-bar"
              value={91}
              text={`${91}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">CSS</h2>
            <CircularProgressbar
              value={87}
              text={`${87}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">JS</h2>
            <CircularProgressbar
              value={82}
              text={`${82}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">React</h2>
            <CircularProgressbar
              value={80}
              text={`${80}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">jQuery</h2>
            <CircularProgressbar
              value={77}
              text={`${77}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
        <div className="col-lg-4 p-4">
          <div className="bg-white rounded-lg shadow p-5">
            <h2 className="h4 font-weight-bold text-center mb-4">Bootstrap</h2>
            <CircularProgressbar
              value={70}
              text={`${70}%`}
              strokeWidth={4}
              styles={buildStyles({
                textColor: "black",
                pathColor: "#b1b493",
                trailColor: "#EEE",
              })}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Skills;
